

.container {
  height: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: auto 1fr 1rem;
      grid-template-rows: auto 1fr 1rem;
}

.board {
  height: 100%;
  display: -ms-grid;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  -ms-grid-columns: min-content;
      grid-template-columns: min-content;
  -ms-grid-rows: max-content (1fr)[7];
      grid-template-rows: max-content repeat(7, 1fr);
  grid-gap: 1px;
  background: rgba(255, 255, 255, .2);
}

.footer {
  background: #333;
}



